.chat-box-container{
    position: fixed;
    width: 25%;
    background-color: white;
    top: 13%;
    right: 2%;
    height: 518px;
    padding: 1rem;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    background-color: #fff;
    overflow-y: auto;
    overflow-x: hidden;
}

.chat-box-conversations{
    height: 83%;
    overflow-y: auto;
    overflow-x: hidden;
}

.chat-box-container{
    padding: 1rem 0 0 0;
    transition: all 0.15s ease-in-out;
    animation: fadeNewMessage 0.5s;
    animation-fill-mode: forwards;
}

.chat-box-message-left{
    float: left;
    text-align: left;
    margin: 0 0 0 1rem;
    color: #fff;
    background-color: #4870df;
}

.chat-box-message-right{
    float: right;
    text-align: right;
    margin: 0 1rem 0 0;
    background-color: #fafafa;
}

.chat-box-message{
    position: relative;
    max-width: 50%;
    min-height: 40px;
    padding: 0.75rem;
    border-radius: 4px;
    font-size: 14px;
    line-height: 18px;
    font-weight: bold;
    box-shadow: 0px 10px 10px -8px rgba(0,0,0,0.08);
    box-sizing: border-box;
    overflow: hidden;
    text-overflow: ellipsis;
}

.chat-box-controls{
    padding-top: 24px;
    height: 16%;
}

.chat-box-input{
    float: left;
    height: 40px;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 40px;
    margin: 0 0.5rem 0 0;
    width: calc(100% - 52px);
    background-color: #dedfed;
    box-shadow: inset 0 0 0 2px #dedfed;
    font-size: 14px;
    font-family: 'Quicksand', sans-serif;
    font-weight: bold;
    transition: 0.15s all ease-in-out;
    box-sizing: border-box;
}

.chat-send-button{
    background-color: #4870df !important;
    height: 38px;
    width: 38px;
}

.ant-layout{
    background-color: rgb(240, 242, 245) !important;
}

.create-feeds{
    background-color: white;
    padding: 33px;
    border-radius: 15px;
}

.create-feeds-label{
   font-weight: bold;
   margin-left: 15px;
   color: #8e8e8e;
}

.create-feeds-header{
   margin-bottom: 16px;
}

.create-feeds-icon{
    color:#4870df !important;
    font-size: 18px;
}

.app-search-input{
    border-radius: 19px;
    background-color: rgb(240, 242, 245);
    padding: 9px;
    border-width: 0;
    font-size: 15px;
}

.app-login-name{
    font-weight: 600;
    color: #050505;
    margin-left: 9px;
}

.app-header-icons{
    color: #3a65ff !important;
    font-size: 20px;
    margin-right: 46px;
    text-align: center;
}

.app-no-feeds{
    margin-top: 61px;
    text-align: center;
    font-weight: bold;
    color: #65676b;
    font-size: 17px;
}